.menu {
  box-sizing: border-box;
  height: 100vh;

  background-color: $header-menu-bg-color;
  font-weight: 700;
  text-transform: uppercase;

  &__top {
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone--number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
    width: fit-content;

    @include hover(transform, scale(1.2));
  }

  &__call--to--order {
    display: block;
    width: fit-content;
    padding-bottom: 3px;
    border-bottom: 1px solid $main-text-color;

    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;

    @include hover(transform, scale(1.2));
  }
}
